* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
  margin-left: 20px;
  margin-top: 20px;
}
.country-list{
  margin-bottom: 20px;
  margin-left: 20px;
}
.country-item{ 
  display: flex;
  align-items: center;
}
.country-item__text{  
  font-size: 16px;
  margin-left: 5px;
  
}
.country-info{
margin: 20px;
}
.country-info__wrap{
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}
.country-info__title{
  font-family:Arial, Helvetica, sans-serif;
  font-size: 28px;
  margin-left: 5px;
}
.country-info__item{
  font-family: Arial, Helvetica, sans-serif;
  font-size: 14px;
}
.country-info__item + .country-info__item{
  margin-top: 10px;
}